<template>
  <div class="views" id="views">
    <div id="container" class="container"></div>
  </div>
</template>

<script>
import { App } from '@/assets/App/App'
import * as THREE from 'three'
import { GUI } from 'three/examples/jsm/libs/dat.gui.module'

const app = new App()
export default {
  data () {
    return {
      params: {
        methods: {
          animation: true, // 循环调用渲染
          renderer: true, // 应用渲染器
          light: true, // 在场景中加入全景光
          postprocessing: true, // 使用后期处理
          controls: true, // 使用轨道控制器，鼠标滚轮控制放大缩小，左键拖动绕中心点旋转，右键拖动中心点。
          statement: true // 在控制台打印声明，与本案例无关，可忽略。
        },
        events: {
          windowResize: true // 监听窗口尺寸变化，并改变渲染页面尺寸
        },
        params: {
          container: null, // 渲染结果输出dom页面
          renderer: [
            'WebGLRenderer'
          ],
          controls: [
            'OrbitControls'
          ],
          rendererParams: {
            antialias: true // 抗锯齿，可忽略。
          },
          postprocessing: [
            'UnrealBloomPass'
          ]
        }
      },
      bloomPassParams: {
        exposure: 1,
        bloomStrength: 1.5,
        bloomThreshold: 0,
        bloomRadius: 0
      }
    }
  },
  mounted () {
    this.params.params.container = document.getElementById('container') // 获取渲染结果输出dom页面
    /**
     * 初始化场景
     */
    app.init(this.params, (scene, camera, renderer) => {
      camera.position.set(1000, 100, 1000) // 修改相机位置
      renderer.toneMapping = THREE.ReinhardToneMapping
    })
    /**
     * 导入模型
     */
    app.addModel({
      url: 'static/model/fbx/cqcet/15/实训楼.FBX', // 模型地址
      type: 'fbx' // 模型格式
    }, () => { // 加载完成回调，可忽略。
      console.log('模型加载完成')
    })
    /**
     * 使用gui调试辉光
     */
    var gui = new GUI()
    this.gui = gui

    gui.add(this.bloomPassParams, 'exposure', 0.1, 2).onChange(function (value) {
      app.renderer.toneMappingExposure = Math.pow(value, 4.0)
    })
    gui.add(this.bloomPassParams, 'bloomThreshold', 0.0, 1.0).onChange(function (value) {
      app.unrealBloomPass.threshold = Number(value)
    })
    gui.add(this.bloomPassParams, 'bloomStrength', 0.0, 3.0).onChange(function (value) {
      app.unrealBloomPass.strength = Number(value)
    })
    gui.add(this.bloomPassParams, 'bloomRadius', 0.0, 1.0).step(0.01).onChange(function (value) {
      app.unrealBloomPass.radius = Number(value)
    })
  },
  beforeDestroy () {
    this.gui.destroy()
  }
}
</script>

<style scoped>
.views {
  height: 100%;
  width: 100%;
}

.container {
  height: 100%;
  width: 100%;
}
</style>
